"use client";

import { useState } from "react";
import { 
  ShoppingBag, Search, Filter, Download, ChevronDown, X
} from "lucide-react";
import { Montserrat } from "next/font/google";
import { motion } from "framer-motion";
import { toast } from "sonner";
import * as XLSX from "xlsx";

const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["500", "600", "700", "800"],
  display: "swap",
});

interface Variant {
  color: string;
  size: string;
  quantity: number;
}

interface Sale {
  id: number;
  productName: string;
  saleDate: string;
  quantity: number;
  totalAmount: number;
  variants: Variant[];
  image?: string;
}

export default function SalesPage() {
  const [sales, setSales] = useState<Sale[]>([
    { 
      id: 1, 
      productName: "Chemise Classique", 
      saleDate: "2024-03-01", 
      quantity: 5, 
      totalAmount: 250.00, 
      variants: [
        { color: "Bleu", size: "M", quantity: 2 },
        { color: "Blanc", size: "L", quantity: 3 },
      ],
      image: "https://via.placeholder.com/50"
    },
    { 
      id: 2, 
      productName: "Pantalon Cargo", 
      saleDate: "2024-03-05", 
      quantity: 3, 
      totalAmount: 180.00, 
      variants: [
        { color: "Noir", size: "S", quantity: 1 },
        { color: "Kaki", size: "M", quantity: 2 },
      ]
    },
    { 
      id: 3, 
      productName: "Robe d'été", 
      saleDate: "2024-03-10", 
      quantity: 7, 
      totalAmount: 350.00, 
      variants: [
        { color: "Rouge", size: "S", quantity: 4 },
        { color: "Jaune", size: "M", quantity: 3 },
      ],
      image: "https://via.placeholder.com/50"
    },
  ]);

  const [searchQuery, setSearchQuery] = useState("");
  const [showFilters, setShowFilters] = useState(false);
  const [sortConfig, setSortConfig] = useState<{ key: string; direction: "asc" | "desc" } | null>(null);
  const [selectedSale, setSelectedSale] = useState<Sale | null>(null);

  const getFilteredSales = () => {
    let filteredSales = [...sales];

    filteredSales = filteredSales.filter(sale => {
      const searchTerms = searchQuery.toLowerCase();
      return sale.productName.toLowerCase().includes(searchTerms);
    });

    if (sortConfig) {
      filteredSales.sort((a, b) => {
        if (sortConfig.key === "productName") {
          return sortConfig.direction === "asc" 
            ? a.productName.localeCompare(b.productName) 
            : b.productName.localeCompare(a.productName);
        }
        const aValue = a[sortConfig.key as keyof Sale];
        const bValue = b[sortConfig.key as keyof Sale];
        if (aValue < bValue) return sortConfig.direction === "asc" ? -1 : 1;
        if (aValue > bValue) return sortConfig.direction === "asc" ? 1 : -1;
        return 0;
      });
    }

    return filteredSales;
  };

  const filteredSales = getFilteredSales();

  const exportToExcel = () => {
    const data = filteredSales.map(sale => ({
      "ID": sale.id,
      "Produit": sale.productName,
      "Date": sale.saleDate,
      "Quantité": sale.quantity,
      "Total": sale.totalAmount.toFixed(2),
    }));

    const worksheet = XLSX.utils.json_to_sheet(data);
    const headerStyle = { font: { bold: true }, fill: { fgColor: { rgb: "D3D3D3" } }, alignment: { horizontal: "center" } };
    const range = XLSX.utils.decode_range(worksheet["!ref"] || "A1:E1");
    for (let col = range.s.c; col <= range.e.c; col++) {
      const cellAddress = XLSX.utils.encode_cell({ r: 0, c: col });
      if (!worksheet[cellAddress]) continue;
      worksheet[cellAddress].s = headerStyle;
    }
    worksheet["!cols"] = [5, 20, 15, 10, 15].map(w => ({ wch: w }));
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Ventes");
    XLSX.writeFile(workbook, "ventes.xlsx");
    toast.success("Liste des ventes exportée avec succès");
  };

  const handleSort = (key: string) => {
    let direction: "asc" | "desc" = "asc";
    if (sortConfig?.key === key && sortConfig.direction === "asc") {
      direction = "desc";
    }
    setSortConfig({ key, direction });
  };

  const handleSaleClick = (sale: Sale) => {
    setSelectedSale(sale);
  };

  const getInitialsAndColor = (productName: string, id: number) => {
    const initials = productName.split(" ").map(word => word[0]).join("").toUpperCase().slice(0, 2);
    const colors = ["bg-blue-600", "bg-emerald-600", "bg-purple-600", "bg-amber-600"];
    return { initials, color: colors[id % colors.length] };
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-200 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        <div className="mb-6 flex items-center gap-3">
          <ShoppingBag className="h-8 w-8 text-black" />
          <h1 className={`text-3xl font-extrabold text-gray-900 tracking-tight ${montserrat.className}`} style={{ fontFamily: "'Montserrat', sans-serif" }}>
            Mes Ventes
          </h1>
        </div>

        <div className="mb-10">
          <p className={`text-lg text-gray-700 ${montserrat.className}`} style={{ fontFamily: "'Montserrat', sans-serif" }}>
            Consultez et gérez vos ventes ici.
          </p>
        </div>

        <div className="bg-white rounded-xl shadow border border-gray-200 overflow-hidden">
          <div className="p-4 border-b border-gray-200">
            <div className="flex items-center justify-between flex-wrap gap-4">
              <div className="flex items-center gap-3">
                <h2 className="text-xl font-bold text-gray-900">Ventes</h2>
              </div>
              <div className="flex items-center gap-2">
                <div className="relative">
                  <input 
                    type="text" 
                    value={searchQuery} 
                    onChange={(e) => setSearchQuery(e.target.value)} 
                    placeholder="Rechercher un produit..." 
                    className="w-full sm:w-64 px-3 py-2 bg-gray-50 border border-gray-200 rounded-lg text-sm focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" 
                  />
                  <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                </div>
                <button onClick={() => setShowFilters(!showFilters)} className="p-2 bg-gray-50 border border-gray-200 rounded-lg text-gray-600 hover:bg-gray-100 transition">
                  <Filter className="h-4 w-4" />
                </button>
                <button onClick={exportToExcel} className="p-2 bg-gray-50 border border-gray-200 rounded-lg text-gray-600 hover:bg-gray-100 transition flex items-center gap-1">
                  <Download className="h-4 w-4" /> <span className="text-sm">Exporter</span>
                </button>
              </div>
            </div>
            {showFilters && (
              <div className="mt-4 p-3 bg-gray-50 rounded-lg border border-gray-200 flex flex-wrap gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Trier par produit</label>
                  <select onChange={(e) => handleSort('productName')} className="px-3 py-2 bg-white border border-gray-200 rounded-md text-sm focus:ring-indigo-500 focus:border-indigo-500">
                    <option value="">Aucun</option>
                    <option value="asc">A-Z</option>
                    <option value="desc">Z-A</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Trier par date</label>
                  <select onChange={(e) => handleSort('saleDate')} className="px-3 py-2 bg-white border border-gray-200 rounded-md text-sm focus:ring-indigo-500 focus:border-indigo-500">
                    <option value="">Aucun</option>
                    <option value="asc">Plus ancien</option>
                    <option value="desc">Plus récent</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Trier par total</label>
                  <select onChange={(e) => handleSort('totalAmount')} className="px-3 py-2 bg-white border border-gray-200 rounded-md text-sm focus:ring-indigo-500 focus:border-indigo-500">
                    <option value="">Aucun</option>
                    <option value="asc">Croissant</option>
                    <option value="desc">Décroissant</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Trier par quantité</label>
                  <select onChange={(e) => handleSort('quantity')} className="px-3 py-2 bg-white border border-gray-200 rounded-md text-sm focus:ring-indigo-500 focus:border-indigo-500">
                    <option value="">Aucun</option>
                    <option value="asc">Croissant</option>
                    <option value="desc">Décroissant</option>
                  </select>
                </div>
              </div>
            )}
          </div>

          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100" onClick={() => handleSort('productName')}>
                    <div className="flex items-center gap-1">
                      Produit
                      {sortConfig?.key === 'productName' && <ChevronDown className={`h-4 w-4 ${sortConfig.direction === 'desc' ? 'transform rotate-180' : ''}`} />}
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100" onClick={() => handleSort('saleDate')}>
                    <div className="flex items-center gap-1">
                      Date
                      {sortConfig?.key === 'saleDate' && <ChevronDown className={`h-4 w-4 ${sortConfig.direction === 'desc' ? 'transform rotate-180' : ''}`} />}
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100" onClick={() => handleSort('quantity')}>
                    <div className="flex items-center gap-1">
                      Quantité
                      {sortConfig?.key === 'quantity' && <ChevronDown className={`h-4 w-4 ${sortConfig.direction === 'desc' ? 'transform rotate-180' : ''}`} />}
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100" onClick={() => handleSort('totalAmount')}>
                    <div className="flex items-center gap-1">
                      Total
                      {sortConfig?.key === 'totalAmount' && <ChevronDown className={`h-4 w-4 ${sortConfig.direction === 'desc' ? 'transform rotate-180' : ''}`} />}
                    </div>
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-100">
                {filteredSales.length > 0 ? (
                  filteredSales.map((sale) => (
                    <tr 
                      key={sale.id} 
                      className="hover:bg-gray-50 transition-colors cursor-pointer" 
                      onClick={() => handleSaleClick(sale)}
                    >
                      <td className="px-4 py-3 whitespace-nowrap">
                        <div className="flex items-center gap-3">
                          {sale.image ? (
                            <img src={sale.image} alt={sale.productName} className="w-10 h-10 rounded-full object-cover border border-gray-200" />
                          ) : (
                            <div className={`w-10 h-10 rounded-full flex items-center justify-center text-white text-sm font-bold ${getInitialsAndColor(sale.productName, sale.id).color}`}>
                              {getInitialsAndColor(sale.productName, sale.id).initials}
                            </div>
                          )}
                          <div className="font-medium text-gray-900">{sale.productName}</div>
                        </div>
                      </td>
                      <td className="px-4 py-3 whitespace-nowrap"><div className="text-sm text-gray-600">{sale.saleDate}</div></td>
                      <td className="px-4 py-3 whitespace-nowrap"><div className="text-sm font-medium text-gray-900">{sale.quantity}</div></td>
                      <td className="px-4 py-3 whitespace-nowrap"><div className="text-sm font-medium text-gray-900">{sale.totalAmount.toFixed(2)}</div></td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={4} className="px-4 py-8 text-center text-gray-500 text-sm">
                      Aucune vente ne correspond à votre recherche.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>

        {selectedSale && (
          <div className="fixed inset-0 bg-black/70 backdrop-blur-md flex items-center justify-center z-50">
            <motion.div
              className="bg-white rounded-2xl shadow-2xl max-w-lg w-full p-6"
              initial={{ opacity: 0, scale: 0.9, y: 20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.9, y: 20 }}
              transition={{ duration: 0.3, ease: "easeOut" }}
            >
              <div className="relative">
                <button onClick={() => setSelectedSale(null)} className="absolute top-2 right-2 text-gray-500 hover:text-gray-800 transition-colors duration-200">✕</button>
                <div className="flex flex-col items-center">
                  {selectedSale.image ? (
                    <img src={selectedSale.image} alt={selectedSale.productName} className="w-32 h-32 rounded-full object-cover border-4 border-gray-200 shadow-lg mb-4" />
                  ) : (
                    <div className={`w-32 h-32 rounded-full flex items-center justify-center border-4 border-gray-200 text-white text-4xl font-bold shadow-lg mb-4 ${getInitialsAndColor(selectedSale.productName, selectedSale.id).color}`}>
                      {getInitialsAndColor(selectedSale.productName, selectedSale.id).initials}
                    </div>
                  )}
                  <h3 className="text-2xl font-bold text-gray-900">{selectedSale.productName}</h3>
                  <div className="w-full space-y-3 mt-4">
                    <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                      <span className="text-gray-700 font-semibold">Date :</span>
                      <span className="text-gray-900">{selectedSale.saleDate}</span>
                    </div>
                    <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                      <span className="text-gray-700 font-semibold">Quantité totale :</span>
                      <span className="text-gray-900">{selectedSale.quantity}</span>
                    </div>
                    <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                      <span className="text-gray-700 font-semibold">Montant total :</span>
                      <span className="text-gray-900">{selectedSale.totalAmount.toFixed(2)}</span>
                    </div>
                    <div className="bg-gray-50 p-3 rounded-lg">
                      <span className="text-gray-700 font-semibold block mb-2">Détails des variantes :</span>
                      {selectedSale.variants.map((variant, index) => (
                        <div key={index} className="flex justify-between items-center text-sm text-gray-900 mb-1">
                          <span>{variant.color} - {variant.size}</span>
                          <span>{variant.quantity} unités</span>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </motion.div>
          </div>
        )}
      </div>
    </div>
  );
}